import Vue from 'vue'
import Vuex from 'vuex'
import ls from '../utils/localStorage'
import router from '../router'

Vue.use(Vuex)

// Áä∂ÊÄÅ
const state = {
  user: ls.getItem('user'),
  //Ê∑ªÂä† auth Êù•‰øùÂ≠òÂΩìÂâçÁî®Êà∑ÁöÑÁôªÂΩïÁä∂ÊÄÅ: ÂÄº‰∏∫ trueÊàñfalse
  auth: ls.getItem('auth'),

  loading:false,
  alter: {
    msg:'',
    msgType:'info',
    msgShow:true,
  },
}

// ÊîπÂèòÁä∂ÊÄÅÁöÑÊñπÊ≥ïÔºå‰∏çÂèØÂºÇÊ≠•
const mutations = {
  UPDATE_USER(state, user){
    state.user = user
    ls.setItem('user', user)
  },
  UPDATE_AUTH(state, auth){
    state.auth = auth
    ls.setItem('auth', auth)
  },

  UPDATE_LOADING(state, loading) {
    state.loading = loading
  },
  UPDATE_ALTER(state, alter) {
    state.alter = alter
  },

}

// Á±ª‰ººmutations Êèê‰∫§ÂâçÈù¢ÁöÑmutations  Ë∞ÉÁî®ÊñπÊ≥ïÔºöstore.dispatch('login)
const actions = {
  login({ commit }, user) {
    if (user) commit('UPDATE_USER', user)
    commit('UPDATE_AUTH', true)
    router.push('/')
  },
  logout({ commit }){
    commit('UPDATE_AUTH', false)
    if(router.history.current.name !== 'Home') 
        router.push({name:'Home', params: {logout:true}})
  },
  updateAlter({state, commit}, alter){
    const stateAlter = state.alter
    if(stateAlter && typeof stateAlter === 'objects'){
      alter = {...stateAlter, ...alter}
    }
    commit('UPDATE_ALTER', alter)
  }


}


export default new Vuex.Store({
  state,
  mutations,
  actions,
  modules:{}
})
